import gql from 'graphql-tag'
import { User as GraphUser } from '@namcbugdb/common'
import { sortBy } from 'lodash'
import { User } from '../../types'

export type ResponseData = {
  sorters: GraphUser[]
}

export const query = gql`
  query {
    sorters {
      id
      firstName
      lastName
    }
  }
`

export const transformData = (data: ResponseData): User[] => {
  const { sorters } = data
  return sortBy(
    sorters.map((user) => ({ ...user, fullName: `${user.firstName} ${user.lastName}` })),
    'fullName'
  )
}
