import gql from 'graphql-tag'

export const transformInput = ({ sampleId }: { sampleId: number }): QueryVariables => ({
  sampleId,
})

type QueryVariables = {
  sampleId: number
}

export const query = gql`
  mutation ($sampleId: ID!) {
    taxIdentificationStart(sampleId: $sampleId)
  }
`

export type ResponseData = {
  taxIdentificationStart: number // NOTE this is records affected, 1 = success, 0 = failure
}

export const transformData = (data: ResponseData): boolean => !!data.taxIdentificationStart
