import gql from 'graphql-tag'

import { LabSortSubmission } from '../../types'

export const transformInput = ({
  sorterCount: count,
  sortTime: time,
  sortDate,
  ...rest
}: LabSortSubmission): QueryVariables => {
  // TODO: this is truly awful code; surely we can do better with dates?
  const sortDateObject = new Date(sortDate)
  const now = new Date()
  const todayDateObject = new Date(
    new Date(now.getTime() - now.getTimezoneOffset() * 60 * 1000).toISOString().substring(0, 10)
  )
  const sortTimestamp = (new Date().getTime() - (todayDateObject.getTime() - sortDateObject.getTime())).toString()

  return {
    count,
    time,
    sortTimestamp,
    ...rest,
  }
}

type QueryVariables = {
  sampleId: number
  sorterId: number
  count: number
  elutriation: boolean
  time: number
  sortTimestamp: string
}

export const query = gql`
  mutation (
    $sampleId: ID!
    $sorterId: ID!
    $count: Int!
    $elutriation: Boolean!
    $time: Float!
    $sortTimestamp: String!
    $labSplit: Float!
  ) {
    labSort(
      sampleId: $sampleId
      sorterId: $sorterId
      count: $count
      elutriation: $elutriation
      time: $time
      sortTimestamp: $sortTimestamp
      labSplit: $labSplit
    )
  }
`

export type ResponseData = {
  labSort: number // NOTE: count of records changed
}

export const transformData = (data: ResponseData): boolean => {
  const { labSort } = data
  return !!labSort
}
