import React, { Suspense } from 'react'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { selectSample, selectMyUser } from '../../data'
import { SampleOverview as Component } from './SampleOverview'
import { Fallback } from './Fallback'
import { navigateState } from '../AppRouter'

const Connector = (): JSX.Element => {
  const { sampleId } = useParams()

  const navigate = useRecoilValue(navigateState)
  const permissions = useRecoilValue(selectMyUser).permissions
  const sample = useRecoilValue(selectSample({ id: Number(sampleId) }))

  if (sample === null) return <h2>Sample {sampleId} not found.</h2>

  return <Component sample={sample} permissions={permissions} navigate={navigate} />
}

export const SampleOverview = (): JSX.Element => (
  <Suspense fallback={<Fallback />}>
    <Connector />
  </Suspense>
)
