import log from 'loglevel'

import { selectMyUserUnprivileged } from '../mock' // TODO: remove this hack

import {
  Taxon,
  Organism,
  UnpersistedOrganism,
  LifeStage,
  NoteType,
  Sample,
  TaxaLevel,
  Box,
  MyUser,
  User,
  LabSortSubmission,
} from '../../types'
import { Api } from './types'

const uniqueNumericId = (() => {
  let id = 0
  return () => id++
})()

export class MockApi implements Api {
  responseDelay = 1000

  constructor() {
    // globalThis.MockApi = this
  }

  wait = async (): Promise<void> => {
    return new Promise((resolve) => {
      window.setTimeout(resolve, this.responseDelay)
    })
  }

  fetchTaxa = async (): Promise<Taxon[]> => {
    return []
  }

  fetchLookups = async (): Promise<{
    taxaLevels: TaxaLevel[]
    lifeStages: LifeStage[]
    noteTypes: NoteType[]
  }> => ({
    taxaLevels: [],
    lifeStages: [],
    noteTypes: [],
  })

  fetchSampleById = async (sampleId: number): Promise<Sample | null> => {
    log.debug('fetchSampleById', sampleId)
    return null
  }

  fetchBoxById = async (boxId: number): Promise<Box | null> => {
    log.debug('fetchBoxById', boxId)
    return null
  }

  upsertOrganism = async (organism: UnpersistedOrganism | Organism): Promise<{ id: number }> => {
    log.debug('MockApi upsertOrganism', organism)
    await this.wait()
    return { id: uniqueNumericId() }
  }

  deleteOrganism = async (organism: Organism): Promise<void> => {
    log.debug('MockApi deleteOrganism', organism)
    await this.wait()
  }

  fetchMyUser = async (): Promise<MyUser> => {
    return selectMyUserUnprivileged()
  }

  fetchSorterUsers = async (): Promise<User[]> => {
    log.debug('MockApi fetchSorterUsers')
    return []
  }

  startTaxonomyIdentification = async (params: { sampleId: number }): Promise<boolean> => {
    log.debug('MockApi startTaxonomyIdentification', params)
    return true
  }

  stopTaxonomyIdentification = async (params: { sampleId: number; time: number }): Promise<boolean> => {
    log.debug('MockApi stopTaxonomyIdentification', params)
    return true
  }

  updateLabSort = async (params: LabSortSubmission): Promise<boolean> => {
    log.debug('MockApi updateLabSort', params)
    return true
  }
}
