/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiButton from '@mui/material/Button'

import { Sample, UserPermissions } from '../../types'

export interface SampleOverviewProps {
  sample: Sample
  permissions: UserPermissions
  navigate: (url: string) => void
}

// TODO: expose navigate function instead of using literal

export const SampleOverview = ({ sample, navigate }: SampleOverviewProps): JSX.Element => {
  // RENDER

  const taxonomyStatus = (() => {
    if (sample.identificationEndDate) return 'Complete'
    if (sample.identificationStartDate) return 'Started'
    return 'Not Started'
  })()

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <div>Sample: {sample.id}</div>
      <div>Box: {sample.boxId}</div>
      <div>Customer: {sample.customerName}</div>
      <div>Box Status: {sample.boxState}</div>
      <div>Sort Status: {sample.sorterId ? 'Complete' : 'Not Started'}</div>
      <div>Taxonomy: {taxonomyStatus}</div>
      <div css={{ marginTop: '1em', display: 'grid', gridTemplateColumns: '15em', gap: '1em' }}>
        <MuiButton variant="outlined" onClick={() => navigate(`/samples/${sample.id}/sorting`)}>
          Sorting
        </MuiButton>
        <MuiButton variant="outlined" onClick={() => navigate(`/samples/${sample.id}/taxonomy`)}>
          Taxonomy
        </MuiButton>
      </div>
    </div>
  )
}
