import gql from 'graphql-tag'
import { User as GraphUser } from '@namcbugdb/common'
import { MyUser } from '../../types'

export type ResponseData = {
  myProfile: GraphUser
}

export const query = gql`
  query {
    myProfile {
      id
      firstName
      lastName
      email
      permissions
    }
  }
`

export const transformData = (data: ResponseData): MyUser => {
  const { myProfile } = data

  return {
    ...myProfile,
    permissions: myProfile.permissions || {},
    fullName: `${myProfile.firstName} ${myProfile.lastName}`,
  }
}
