import gql from 'graphql-tag'

export const transformInput = ({ sampleId, time }: { sampleId: number; time: number }): QueryVariables => ({
  sampleId,
  time,
})

type QueryVariables = {
  sampleId: number
  time: number
}

export const query = gql`
  mutation ($sampleId: ID!, $time: Float!) {
    taxIdentificationStop(sampleId: $sampleId, time: $time)
  }
`

export type ResponseData = {
  taxIdentificationStop: number // NOTE this is records affected, 1 = success, 0 = failure
}

export const transformData = (data: ResponseData): boolean => !!data.taxIdentificationStop
