/** @jsxImportSource @emotion/react */
import React from 'react'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select, { SelectChangeEvent } from '@mui/material/Select'

import { CommonFieldProps } from '../../types'

export interface SelectFieldProps extends CommonFieldProps<string | number> {
  options: {
    value: string | number
    label: React.ReactNode
    disabled?: boolean
  }[]
}

export const SelectField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  label,
  onChange,
  readOnly,
  value,

  // SelectFieldProps
  options,
}: SelectFieldProps): JSX.Element => {
  const handleChange = ({ target: { value: newValue } }: SelectChangeEvent) => {
    onChange(newValue)
  }

  return (
    <FormControl
      sx={{ minWidth: '10em' }}
      className={className}
      disabled={disabled || readOnly}
      css={{ '.MuiInputBase-root': { color: 'unset' } }}
    >
      {label && (
        <InputLabel
        // size="small" // typing says this isn't a prop, but
        >
          {label}
        </InputLabel>
      )}
      <Select
        error={error}
        label={label}
        onChange={handleChange}
        value={String(value)}
        size="small"
        readOnly={readOnly}
      >
        {options.map((option) => (
          <MenuItem key={option.value} value={option.value} disabled={option.disabled}>
            {option.label || <span css={{ fontStyle: 'italic' }}>(clear)</span>}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  )
}
