import gql from 'graphql-tag'

import { Organism } from '../../types'

export const transformInput = (organism: Organism): QueryVariables => ({
  organismId: organism.id,
})

type QueryVariables = {
  organismId: number
}

export const query = gql`
  mutation ($organismId: ID!) {
    deleteOrganism(organismId: $organismId)
  }
`

export type ResponseData = {
  deleteOrganism: number // NOTE this is records affected, not an ID!
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export const transformData = (data: ResponseData): void => undefined
