/* eslint-disable @typescript-eslint/no-non-null-assertion */
/** @jsxImportSource @emotion/react */
import React, { useEffect, useReducer } from 'react'
import produce from 'immer'
import { NAMCResource } from '@namcbugdb/common'

import { useApi } from '../../data/api'
import { User, Sample, MyUser } from '../../types'
import { SampleInfoPanel } from '../SampleInfoPanel'
import { SelectField, SelectFieldProps } from '../SelectField'
import { NumberField } from '../NumberField'
import { CheckboxField } from '../CheckboxField'
import { DateField } from '../DateField'
import { ConfirmNavigateAway } from '../AppRouter'
import { ToggleEditModeButton } from './ToggleEditModeButton'

export interface SampleSortingProps {
  sorterUsers: User[]
  sample: Sample
  user: MyUser
}

interface Fields {
  sorterId: number | ''
  labSplitPercent: number | null
  sorterCount: number | null
  sortTime: number | null
  sortDate: string | null
  elutriation: boolean | null
}

type ReducerState = {
  sample?: Sample
  userId?: number
  values: Fields
  errors: Partial<Record<keyof Fields, boolean>>
  editMode: 'stopped' | 'started' | 'stopping'
}

type ReducerAction =
  | { type: 'reset'; payload: { sample: Sample; user: MyUser } }
  | { type: 'setFieldValue'; payload: Partial<Fields> }
  | { type: 'setEditMode'; payload: ReducerState['editMode'] }

const initialState = {
  values: { sorterId: '', labSplitPercent: null, sorterCount: null, sortTime: null, sortDate: '', elutriation: false },
  editMode: 'stopped',
} as ReducerState

const reducer = (state: ReducerState, action: ReducerAction): ReducerState => {
  if (action.type === 'reset') {
    const {
      payload: { sample, user },
    } = action

    const { sorterId, sorterCount, sortTime, sortDate, labSplit, elutriation } = sample

    return {
      ...initialState,
      sample,
      userId: user.id,
      values: {
        sorterId: sorterId || '',
        sorterCount,
        sortTime,
        sortDate: sortDate && sortDate?.substring(0, 10),
        labSplitPercent: labSplit && labSplit * 100,
        elutriation: !!elutriation,
      },
    }
  }

  return produce(state, (draft) => {
    switch (action.type) {
      case 'setFieldValue': {
        Object.entries(action.payload).forEach(([fieldName, value]) => (draft.values[fieldName] = value))
        break
      }
      case 'setEditMode': {
        const editMode = action.payload
        draft.editMode = editMode
        if (editMode !== 'started') break
        if (!draft.sample?.sorterId) draft.values.sorterId = draft.userId || ''

        if (!draft.sample?.sortDate) {
          const now = new Date()
          draft.values.sortDate = new Date(now.getTime() - now.getTimezoneOffset() * 60 * 1000)
            .toISOString()
            .substring(0, 10)
        }
        break
      }
    }
  })
}

export interface ControllerActions {
  reset: (payload: { sample: Sample; user: MyUser }) => void
  setFieldValue: (fieldName: keyof Fields, value: any) => void
  startSorting: () => void
  stopSorting: () => void
}

const useComponentController = (): [ReducerState, ControllerActions] => {
  const api = useApi()

  const [state, dispatch] = useReducer(reducer, initialState)

  const actions = {
    reset: (payload: { sample: Sample; user: MyUser }) => {
      dispatch({ type: 'reset', payload })
    },
    setFieldValue: (fieldName: keyof Fields, value: any) => {
      dispatch({
        type: 'setFieldValue',
        payload: { [fieldName]: value },
      })
    },
    startSorting: () => {
      dispatch({
        type: 'setEditMode',
        payload: 'started',
      })
    },
    stopSorting: async () => {
      dispatch({
        type: 'setEditMode',
        payload: 'stopping',
      })
      const { sorterId, labSplitPercent, sorterCount, sortTime, sortDate, elutriation } = state.values
      if (
        // TODO: fix this awful guard
        !state.sample?.id ||
        sorterId === '' ||
        labSplitPercent === null ||
        sorterCount === null ||
        sortTime === null ||
        sortDate === null ||
        elutriation === null
      )
        throw new Error('Missing values.')
      await api.updateLabSort({
        sampleId: state.sample.id,
        sorterId,
        labSplit: labSplitPercent / 100,
        sorterCount,
        sortTime,
        sortDate,
        elutriation,
      })
      dispatch({
        type: 'setEditMode',
        payload: 'stopped',
      })
    },
  }

  const errors =
    state.editMode === 'stopped'
      ? {}
      : {
          sortUserId: !state.values.sorterId,
          labSplitPercent: state.values.labSplitPercent === null,
          sorterCount: state.values.sorterCount === null,
          sortTime: state.values.sortTime === null,
        }

  return [{ ...state, errors }, actions]
}

export const SampleSorting = ({ sorterUsers, sample, user }: SampleSortingProps): JSX.Element => {
  // STATE

  const [state, actions] = useComponentController()
  const {
    values: { sorterId, labSplitPercent, sorterCount, sortTime, sortDate, elutriation },
    errors,
    editMode,
  } = state
  const { setFieldValue } = actions
  const isEditing = editMode !== 'stopped'

  // EFFECTS

  useEffect(() => {
    actions.reset({ sample, user })
  }, [sample])

  // RENDER

  const sorterOptions: SelectFieldProps['options'] = sorterUsers.map((sorterUser) => ({
    value: sorterUser.id,
    label: sorterUser.fullName,
  }))
  if (sample.sorterId && !sorterUsers.find((sorterUser) => sorterUser.id === sorterId))
    sorterOptions.push({
      value: sorterId,
      label: sample.sorterName,
    })

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <SampleInfoPanel
          sample={sample}
          actions={
            <ToggleEditModeButton
              sample={sample}
              isEditing={editMode === 'started'}
              actions={actions}
              hasPermission={user.permissions.updateAny?.includes(NAMCResource.sorting)}
              disabled={Object.values(errors).some((value) => value)}
            />
          }
        />
        <div css={{ display: 'grid', gridTemplateColumns: '20em', gap: '1em' }}>
          <SelectField
            label="Sorter"
            value={sorterId}
            onChange={(value) => setFieldValue('sorterId', value)}
            options={sorterOptions}
            error={errors.sorterId}
            readOnly={!isEditing}
          />
          <NumberField
            label="Split %"
            value={labSplitPercent}
            onChange={(value) => setFieldValue('labSplitPercent', value)}
            min={0}
            max={100}
            error={errors.labSplitPercent}
            readOnly={!isEditing}
          />
          <NumberField
            label="Number of bugs"
            value={sorterCount}
            onChange={(value) => setFieldValue('sorterCount', value)}
            min={0}
            error={errors.sorterCount}
            readOnly={!isEditing}
          />
          <CheckboxField
            label="Elutriation"
            value={elutriation || false}
            onChange={(value) => setFieldValue('elutriation', value)}
            readOnly={!isEditing}
          />
          <NumberField
            label="Sort time (hours)"
            value={sortTime}
            onChange={(value) => setFieldValue('sortTime', value)}
            min={0}
            error={errors.sortTime}
            readOnly={!isEditing}
          />
          <DateField
            label="Date sorted"
            value={sortDate}
            onChange={(value) => setFieldValue('sortDate', value)}
            error={errors.sortDate}
            readOnly={!isEditing}
          />
        </div>
      </div>
      {editMode !== 'stopped' && (
        <ConfirmNavigateAway message="Identification is still in progress. Are you sure you want to leave?" />
      )}
    </>
  )
}
