import gql from 'graphql-tag'

import { UnpersistedOrganism, Organism } from '../../types'

export const transformInput = (organism: UnpersistedOrganism | Organism): QueryVariables => {
  return { lifeStageId: -1, ...organism } // TODO: fix this TS hack (lifeStageId always present when saving)
}

type QueryVariables = {
  sampleId: number
  taxonId: number
  lifeStageId: number
  splitCount: number
  bigRareCount: number | null
  comments: string
  noteTypeIds: number[]
}

export const query = gql`
  mutation (
    $sampleId: ID!
    $taxonId: ID!
    $lifeStageId: ID!
    $splitCount: Int!
    $bigRareCount: Int
    $comments: String
    $noteTypeIds: [Int]
  ) {
    createSampleTaxaInfo(
      sampleId: $sampleId
      taxonomyId: $taxonId
      lifeStageId: $lifeStageId
      splitCount: $splitCount
      comments: $comments
      noteTypeIds: $noteTypeIds
      bigRareCount: $bigRareCount
    )
  }
`

export type ResponseData = {
  createSampleTaxaInfo: number
}

export const transformData = (data: ResponseData): { id: number } => {
  const { createSampleTaxaInfo: id } = data

  return { id }
}
