import React from 'react'
import { useRecoilValue } from 'recoil'

import { QuickNavigateField as Component } from './QuickNavigateField'
import { navigateState } from '../AppRouter'

export interface QuickNavigateFieldProps {
  // TODO: figure out best TS pattern for prop differences
  className?: string
}

export const QuickNavigateField = (props: QuickNavigateFieldProps): JSX.Element => {
  const navigate = useRecoilValue(navigateState)

  return <Component navigate={navigate} {...props} />
}
