/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { HashRouter as Router, Switch, Route, useHistory } from 'react-router-dom'
import { atom, useSetRecoilState } from 'recoil'
import log from 'loglevel'
import { SampleOverview } from '../SampleOverview'
import { SampleSorting } from '../SampleSorting'
import { SampleTaxonomyIdentification } from '../SampleTaxonomyIdentification'
import { AppHeader } from '../AppHeader'
import { SideNavBar } from '../SideNavBar'
import { ErrorBoundary } from '../ErrorBoundary'

export const navigateState = atom({
  key: 'AppRouter/navigate',
  default: (url) => {
    log.debug('navigate', url)
  },
})

export type NavigateFn = (route: string, params: Record<string, any>) => void

export const AppRouter = (): JSX.Element => {
  const setNavigate = useSetRecoilState(navigateState)
  const history = useHistory()

  useEffect(() => {
    const handleNavigate: NavigateFn = (route) => {
      history.push(route)
      // TODO: use proper callback
    }

    setNavigate(() => handleNavigate)
  }, [])

  return (
    <div css={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
      <AppHeader />
      <SideNavBar />
      <div css={{ flex: 1, padding: '0.5em' }}>
        <ErrorBoundary>
          <Router>
            <Switch>
              <Route exact path="/">
                <div css={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                  Use Quick Open to find samples.
                </div>
              </Route>
              <Route exact path="/samples/:sampleId">
                <SampleOverview />
              </Route>
              <Route exact path="/samples/:sampleId/sorting">
                <SampleSorting />
              </Route>
              <Route exact path="/samples/:sampleId/taxonomy">
                <SampleTaxonomyIdentification />
              </Route>
              <Route path="*">
                <h2>Page not found.</h2>
              </Route>
            </Switch>
          </Router>
        </ErrorBoundary>
      </div>
    </div>
  )
}
