import gql from 'graphql-tag'
import { BoxInfo as GraphBoxInfo } from '@namcbugdb/common'
import { Box } from '../../types'

export type ResponseData = {
  boxInfo: GraphBoxInfo
}

export const query = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      boxId
      measurements
    }
  }
`

export const transformData = (data: ResponseData): Box => {
  const { boxInfo } = data

  return {
    id: boxInfo.boxId,
    measurements: boxInfo.measurements,
  }
}
