import { api } from '@namcbugdb/common'
import { ASTNode } from 'graphql'
import { endpoints } from '../../config'
import { UseHostedReturn } from '@namcbugdb/react-common'
import log from 'loglevel'

import {
  Taxon,
  UnpersistedOrganism,
  LifeStage,
  NoteType,
  Sample,
  Organism,
  User,
  TaxaLevel,
  Box,
  MyUser,
  LabSortSubmission,
} from '../../types'
import { Api } from './types'

import * as fetchSampleById from './fetchSampleById'
import * as fetchTaxa from './fetchTaxa'
import * as fetchBoxById from './fetchBoxById'
import * as fetchLookups from './fetchLookups'
import * as fetchMyUser from './fetchMyUser'
import * as upsertOrganism from './upsertOrganism'
import * as deleteOrganism from './deleteOrganism'
import * as startTaxonomyIdentification from './startTaxonomyIdentification'
import * as stopTaxonomyIdentification from './stopTaxonomyIdentification'
import * as updateLabSort from './updateLabSort'
import * as fetchSorterUsers from './fetchSorterUsers'

// const uniqueNumericId = (() => {
//   let id = 0
//   return () => id++
// })()

class GraphError extends Error {
  constructor(message) {
    super()
    this.message = message
  }

  toString() {
    return this.message
  }
}

export class GraphApi implements Api {
  auth: UseHostedReturn

  constructor(auth: UseHostedReturn) {
    this.auth = auth
  }

  send = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const jwt = await this.auth.getCurrentToken()
    const response = await api.apiCall<T>(endpoints.graphql as string, jwt, query, variables)
    const { data, errors } = response
    if (errors || !data) {
      log.debug(errors)
      throw new GraphError(errors?.map((error) => error.message).join('\n'))
    }
    return data
  }

  fetchTaxa = async (): Promise<Taxon[]> => {
    const { query, transformData } = fetchTaxa

    let offset: number | null | undefined = 0
    const completeData: fetchTaxa.ResponseData = {
      taxonomy: { records: [] },
    }

    while (offset !== null) {
      const {
        taxonomy: { records, nextOffset },
      } = await this.send<fetchTaxa.ResponseData>(query, { offset })

      completeData.taxonomy.records.push(...records)
      offset = nextOffset
    }

    return transformData(completeData)
  }

  fetchLookups = async (): Promise<{
    taxaLevels: TaxaLevel[]
    lifeStages: LifeStage[]
    noteTypes: NoteType[]
  }> => {
    const { query, transformData } = fetchLookups
    const data = await this.send<fetchLookups.ResponseData>(query)
    return transformData(data)
  }

  fetchSampleById = async (sampleId: number): Promise<Sample | null> => {
    const { query, transformData } = fetchSampleById
    const data = await this.send<fetchSampleById.ResponseData>(query, { sampleId })
    if (data.samples.records.length === 0) return null
    return transformData(data)
  }

  fetchBoxById = async (boxId: number): Promise<Box | null> => {
    const { query, transformData } = fetchBoxById
    const data = await this.send<fetchBoxById.ResponseData>(query, { boxId })
    if (!data.boxInfo) return null
    return transformData(data)
  }

  fetchMyUser = async (): Promise<MyUser> => {
    const { query, transformData } = fetchMyUser
    const data = await this.send<fetchMyUser.ResponseData>(query)
    return transformData(data)
  }

  fetchSorterUsers = async (): Promise<User[]> => {
    const { query, transformData } = fetchSorterUsers
    const data = await this.send<fetchSorterUsers.ResponseData>(query)
    return transformData(data)
  }

  upsertOrganism = async (organism: UnpersistedOrganism | Organism): Promise<{ id: number }> => {
    const { query, transformInput, transformData } = upsertOrganism
    const variables = transformInput(organism)
    const data = await this.send<upsertOrganism.ResponseData>(query, variables)
    return transformData(data)
  }

  deleteOrganism = async (organism: Organism): Promise<void> => {
    const { query, transformInput, transformData } = deleteOrganism
    const variables = transformInput(organism)
    const data = await this.send<deleteOrganism.ResponseData>(query, variables)
    return transformData(data)
  }

  startTaxonomyIdentification = async (params: { sampleId: number }): Promise<boolean> => {
    const { query, transformInput, transformData } = startTaxonomyIdentification
    const variables = transformInput(params)
    const data = await this.send<startTaxonomyIdentification.ResponseData>(query, variables)
    return transformData(data)
  }

  stopTaxonomyIdentification = async (params: { sampleId: number; time: number }): Promise<boolean> => {
    const { query, transformInput, transformData } = stopTaxonomyIdentification
    const variables = transformInput(params)
    const data = await this.send<stopTaxonomyIdentification.ResponseData>(query, variables)
    return transformData(data)
  }

  updateLabSort = async (params: LabSortSubmission): Promise<boolean> => {
    const { query, transformInput, transformData } = updateLabSort
    const variables = transformInput(params)
    const data = await this.send<updateLabSort.ResponseData>(query, variables)
    return transformData(data)
  }
}
