import noteTypes from './noteTypes.json'
import taxa from './taxa.json'
import lookups from './lookups.json'
import * as fetchSampleById from '../api/fetchSampleById'
import fetchSampleByIdMockData from './fetchSampleById.mock.json'

import { Taxon, LifeStage, NoteType, Sample, MyUser } from '../../types'
import { withDescendantIds } from '../../lib/taxonomy'
import { NAMCResource } from '@namcbugdb/common'

export const selectLifeStages = (): LifeStage[] =>
  lookups.data.lookups.taxa.lifeStages.sort((a, b) => a.rankOrder - b.rankOrder)

export const selectNoteTypes = (): NoteType[] =>
  noteTypes.map(({ note_id: id, abbreviation, note_name: name, is_active: isActive }) => ({
    id,
    name,
    abbreviation,
    isActive,
  }))

export const selectTaxa = (): Taxon[] => {
  const taxaLevels = lookups.data.lookups.taxa.taxaLevels
  return withDescendantIds(
    taxa.map<Taxon>(
      ({
        scientific_name: name,
        taxonomy_id: id,
        level_id: levelId,
        level_name: levelName,
        parent_taxonomy_id: parentId,
        synonyms,
      }) => ({
        id,
        name,
        levelId,
        levelName,
        levelRankOrder: taxaLevels.find(({ id }) => id === levelId)?.rankOrder ?? -1,
        parentId,
        synonyms: synonyms ? JSON.parse(synonyms) : [],
      })
    )
  )
}

export const selectSamples = (): Sample[] => [
  fetchSampleById.transformData(fetchSampleByIdMockData.data as fetchSampleById.ResponseData),
]

