import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample, SampleTaxaInfo as GraphSampleTaxaInfo } from '@namcbugdb/common'
import { Sample } from '../../types'

export type ResponseData = {
  samples: PaginatedRecords<GraphSample>
  sampleTaxaInfo: PaginatedRecords<GraphSampleTaxaInfo>
}

export const query = gql`
  query ($sampleId: Int!) {
    samples(sampleIds: [$sampleId]) {
      records {
        sampleId
        boxId
        boxState
        measurements
        customerName
        sampleDate
        siteName
        usState
        idErId
        idStartDate
        idEndDate
        sorterId
        sorterName
        sortTime
        sortDate
        sorterCount
        elutriation
        labSplit
        measurements
      }
    }
    sampleTaxaInfo(sampleIds: [$sampleId]) {
      records {
        sampleId
        organismId
        lifeStageId
        taxonomyId
        levelId
        levelName
        splitCount
        noteTypeIds
        comments
      }
    }
  }
`

export const transformData = (data: ResponseData): Sample => {
  const sample = data.samples.records[0]
  const organisms = data.sampleTaxaInfo.records

  const {
    sampleId: id,
    idErId: identificationUserId,
    idStartDate: identificationStartDate,
    idEndDate: identificationEndDate,
    boxState,
    ...restSample
  } = sample

  return {
    ...restSample,
    id,
    identificationUserId,
    identificationStartDate,
    identificationEndDate,
    boxState,
    organisms: organisms.map((organism) => ({
      ...organism,
      id: organism.organismId,
      taxonId: organism.taxonomyId,
      noteTypeIds: organism.noteTypeIds.filter((noteTypeId) => noteTypeId !== null),
    })),
  }
}
