import gql from 'graphql-tag'
import {
  TaxaLevelLookup as GraphTaxaLevelLookup,
  LifeStageLookup as GraphLifeStageLookup,
  NoteTypes as GraphNoteTypes,
} from '@namcbugdb/common'
import { LifeStage, NoteType, TaxaLevel } from '../../types'

export type ResponseData = {
  lookups: {
    taxa: {
      taxaLevels: GraphTaxaLevelLookup[]
      lifeStages: GraphLifeStageLookup[]
    }
    sample: {
      noteTypes: GraphNoteTypes[]
    }
  }
}

export const query = gql`
  query {
    lookups {
      taxa {
        lifeStages
        taxaLevels
      }
      sample {
        noteTypes
      }
    }
  }
`

export const transformData = (
  data: ResponseData
): {
  taxaLevels: TaxaLevel[]
  lifeStages: LifeStage[]
  noteTypes: NoteType[]
} => {
  const {
    lookups: {
      taxa: { taxaLevels, lifeStages },
      sample: { noteTypes },
    },
  } = data
  return {
    taxaLevels,
    lifeStages,
    noteTypes,
  }
}
