/** @jsxImportSource @emotion/react */
import React, { useState, useEffect } from 'react'
import { App } from '../App'
import { useHostedAuth, UseHostedReturn } from '@namcbugdb/react-common'
import { appDefaults, endpoints, redirectUrl } from '../config'
import { api } from '@namcbugdb/common'
import CircularProgress from '@mui/material/CircularProgress'
import log from 'loglevel'

const AppAuthConnect: React.FC = () => {
  const [authInfo, setAuthInfo] = useState<Record<string, unknown>>()
  const [isAppPreLoading, setIsAppPreLoading] = useState(true)
  const auth: UseHostedReturn = useHostedAuth(authInfo as Record<string, unknown>, false, false) //config.isDev

  // This is the pre-auth step to retrieve all the necessary cognito endpoint data
  useEffect(() => {
    api
      .authCheck(endpoints.graphql as string, '', appDefaults.apiTimeout)
      .then((authResp) => {
        log.debug('authResp', authResp)
        const authVals = authResp.data?.auth
        if (authVals && authVals.region) {
          setAuthInfo({
            region: authVals.region,
            userPoolId: authVals.userPool,
            userPoolWebClientId: authVals.clientId,
            mandatorySignIn: true,
            oauth: {
              domain: authVals.domain,
              scope: ['email', 'profile', 'openid'],
              redirectSignIn: redirectUrl.toString(),
              redirectSignOut: redirectUrl.toString(),
              responseType: 'code',
            },
          })
          setIsAppPreLoading(false)
        }
      })
      .catch((err) => {
        log.error('api.getPrograms error', err)
      })
  }, [])

  // This is just an example of a query. Currently it doesn't really do anything but dump out to the console.
  // useEffect(() => {
  //   log.debug('Metric Query:', auth.isAuthenticated)
  //   if (!auth.isAuthenticated) return
  //   // First we need to get the auth token. This is stored in a cookie if it exists
  //   // So it's really only a bottleneck the first time.
  //   auth
  //     .getCurrentToken()
  //     // Now the actual query
  //     .then((jwt) => api.metrics(endpoints.graphql as string, jwt, 100, 0))
  //     .then((data) => log.debug('Metric Query:', data))
  //     .catch((err) => log.error('Metric Query Error', err))
  // }, [auth.isAuthenticated])

  // Some kind of spinner for pre-launch
  if (isAppPreLoading) {
    return (
      <div
        css={{
          height: '100vh',
          width: '100%',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <CircularProgress />
      </div>
    )
  }
  //
  else {
    if (!auth.isAuthenticated) {
      if (!auth.isAuthComplete) {
        return <div>Authenticating...</div>
      }
      return (
        <div>
          <button onClick={auth.signIn}>Sign In</button>
        </div>
      )
    } else {
      return <App signOut={auth.signOut} auth={auth} />
    }
  }
}
export default AppAuthConnect
