import React from 'react'
import MuiButton from '@mui/material/Button'
import MuiTooltip from '@mui/material/Tooltip'
import EditIcon from '@mui/icons-material/Edit'
import StopIcon from '@mui/icons-material/Stop'

import { Sample } from '../../types'
import { ControllerActions } from './SampleSorting'

interface ToggleEditModeButtonProps {
  hasPermission?: boolean
  sample: Sample
  isEditing: boolean
  actions: ControllerActions
  disabled?: boolean
}

export const ToggleEditModeButton = ({
  hasPermission,
  sample,
  isEditing,
  actions,
  disabled,
}: ToggleEditModeButtonProps): JSX.Element | null => {
  if (!hasPermission) return null

  const error = (() => {
    if (sample.boxState !== 'Active')
      return 'The sorting for this sample cannot be edited because the box it is associated with is not currently active.'
  })()

  if (error)
    return (
      <MuiTooltip title={error}>
        <span>
          <MuiButton disabled variant="outlined" startIcon={<EditIcon />}>
            Start Sorting
          </MuiButton>
        </span>
      </MuiTooltip>
    )

  return (
    <MuiButton
      onClick={() => {
        if (isEditing) {
          actions.stopSorting()
        } else {
          actions.startSorting()
        }
      }}
      startIcon={isEditing ? <StopIcon /> : <EditIcon />}
      variant={isEditing ? 'contained' : 'outlined'}
      disabled={disabled}
    >
      {isEditing ? 'Stop Sorting' : 'Start Sorting'}
    </MuiButton>
  )
}
